.statistics {
  width: 350px;
  margin-left: auto;
  margin-right: auto;
  padding-top: 10px;
  background-color: rgb(253, 232, 232);
}

.title {
  margin-bottom: 30px;
}

.statList {
  display: flex;
  justify-content: space-between;
  padding: 0;
  list-style-type: none;
}
.item {
  display: flex;
  flex-basis: 100%;
  flex-direction: column;
  padding-top: 10px;
  padding-bottom: 10px;
  outline: 1px solid gray;
}
